// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchSubscriberList(page) {
	return instance.get(`api/newsLetter/all/${page}`);
}

function fetchSubscriberTotalCount() {
	return instance.get(`/api/newsLetter/totalCount`);
}

function searchSubscribeDate(start, end) {
	return instance.get(`api/newsLetter/search/${start}/${end}`);
}

function addNewSubscribe(data) {
	return instanceWithAuth.pos('api/newsletter/newSubscribe', data);
}

function deleteSubscribe(id) {
	return instanceWithAuth.delete(`api/newsletter/deleteSubscribe/${id}`);
}

export {
	fetchSubscriberList,
	fetchSubscriberTotalCount,
	searchSubscribeDate,
	addNewSubscribe,
	deleteSubscribe,
};
