<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/newsletter/1')" class="current">뉴스레터</li>
		</ul>
		<div class="search-container">
			<p class="body2">구독 일자 검색</p>
			<input
				type="date"
				class="admin-input"
				placeholder="YYYY.MM.DD"
				v-model="start_date"
			/>
			<p class="body2 range">부터</p>
			<input
				type="date"
				class="admin-input"
				placeholder="YYYY.MM.DD"
				v-model="end_date"
			/>
			<p class="body2 range">까지</p>
			<button class="outlined-btn" @click="searchSubscribeDate">검색</button>
		</div>

		<div class="contents-container">
			<div class="tab-contents">
				<ul class="table board">
					<li class="row props">
						<div class="prop email">이메일</div>
						<div class="prop date">등록일자</div>
					</li>

					<li
						class="row no-data body3"
						v-if="subscriberList.length === 0"
						style="display: block;"
					>
						등록된 뉴스레터가 없습니다
					</li>
					<ul v-for="subscriberItem in subscriberList" :key="subscriberItem.id">
						<li class="row">
							<div class="prop email txt-left">{{ subscriberItem.email }}</div>
							<div class="prop date">
								{{ subscriberItem.createDate }}
							</div>
						</li>
					</ul>
				</ul>
			</div>
		</div>
		<Pagination
			v-if="totalPage !== -1"
			:listRowCt="15"
			:pageLinkCt="5"
			:totalCount="totalPage"
			:url="`api/newsLetter/all`"
		/>
	</div>
</template>
<script>
import {
	fetchSubscriberList,
	fetchSubscriberTotalCount,
	searchSubscribeDate,
} from '@/api/newsletter';
import Pagination from '@/components/common/Pagination';

export default {
	components: { Pagination },
	created() {
		this.currentPage = this.$route.params.page;
		this.fetchSubscriberList(this.currentPage);
		this.fetchSubscriberTotalCount();
	},
	data() {
		return {
			start_date: '',
			end_date: '',
			subscriberList: [],
			totalPage: -1,
			currentPage: 1,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchSubscriberList(page) {
			const { data } = await fetchSubscriberList(page - 1);
			this.subscriberList = data;
		},
		async fetchSubscriberTotalCount() {
			const { data } = await fetchSubscriberTotalCount();
			this.totalPage = data.totalCount;
		},
		async searchSubscribeDate() {
			try {
				const { data } = await searchSubscribeDate(
					this.start_date,
					this.end_date,
				);
				this.subscriberList = data;
			} catch (e) {
				alert('에러가 발생했습니다 ');
			}
		},
	},
};
</script>
<style scoped>
.tabs.sub {
	margin-bottom: 40px;
}
.contents-container {
	padding-top: 30px;
}
.search-container {
	display: flex;
	align-items: center;
	padding-left: 20px;
}

.search-container p:nth-child(1) {
	width: 100px;
	margin-right: 10px;
}
.search-container .range {
	margin: 0 10px;
}
.search-container button {
	margin-left: 10px;
}

.row {
	justify-content: space-between;
}
.prop.email {
	width: 415px;
}
.prop.email.txt-left {
	padding-left: 20px;
}
.prop.date {
	width: 200px;
}
</style>
